export default {
  name: "uni-rate",
  components: {
    uniIcon
  },
  props: {
    isFill: {
      //星星的类型，是否镂空
      type: [Boolean, String],
      default: true
    },
    color: {
      //星星的颜色
      type: String,
      default: "#ececec"
    },
    activeColor: {
      //星星选中状态颜色
      type: String,
      default: "#D62222"
    },
    size: {
      //星星的大小
      type: [Number, String],
      default: 24
    },
    value: {
      //当前评分
      type: [Number, String],
      default: 5
    },
    max: {
      //最大评分
      type: [Number, String],
      default: 5
    },
    margin: {
      //星星的间距
      type: [Number, String],
      default: 0
    },
    disabled: {
      //是否可点击
      type: [Boolean, String],
      default: false
    }
  },
  data() {
    return {
      maxSync: this.max,
      valueSync: this.value
    };
  },
  computed: {
    stars() {
      const max = Number(this.maxSync) ? Number(this.maxSync) : 5;
      const value = Number(this.valueSync) ? Number(this.valueSync) : 0;
      const starList = [];
      const floorValue = Math.floor(value);
      const ceilValue = Math.ceil(value);
      for (let i = 0; i < max; i++) {
        if (floorValue > i) {
          starList.push({
            activeWitch: "100%"
          });
        } else if (ceilValue - 1 === i) {
          starList.push({
            activeWitch: (value - floorValue) * 100 + "%"
          });
        } else {
          starList.push({
            activeWitch: "0"
          });
        }
      }
      return starList;
    }
  },
  methods: {
    onClick(index) {
      if (this.disabled === true || this.disabled === "true") {
        return;
      }
      this.valueSync = index + 1;
      this.$emit("change", {
        value: this.valueSync
      });
      // (this.valueSync)
    }
  }
};
